/* INTRO PAGE */
.intro{
    height:100vh;
    width: 100%;
    margin: auto;
    background-image: url(/public/images/dark-evening.jpg);
    display: table;
    top: 0;
    background-size: cover;
}
.intro .inner {
    display: table-cell;
    vertical-align: middle;
    width: 100%;
    max-width: none;
}

.content {
    max-width: 500px;
    margin: 0 auto;
    text-align: center;
}

.content h1 {
    font-family: 'rust';
    color: rgba(255, 255, 255, 0.445);
    font-size: 700%;
    text-align: center;

    -webkit-animation: fadein 10s;
            animation: fadein 10s;
}

.intro-btn  {
    border-radius: 10px;
    font-family: 'rust';
    color: rgba(255, 255, 255, 0.445);
    font-size: 300%;
    padding: 10px 20px;
    border: solid rgba(255, 255, 255, 0.445) 3px;
    text-transform: uppercase;
    -webkit-box-decoration-break: none;
            box-decoration-break: none;
    -webkit-animation: fadein 25s;
            animation: fadein 25s;

}
.intro-btn:hover {
    color: #ffffff;
    border: solid #ffffff 3px;
}
@-webkit-keyframes fadein {
    from { opacity: 0;}
    to   { opacity: 1;}
}
@keyframes fadein {
    from { opacity: 0;}
    to   { opacity: 1;}
}

@media (max-width: 768px){
    .intro{
        background-size:cover, 100vh 100vw;
        
    }
    .content h1{
        font-size: 300%;
    }
    .intro-btn  {
        font-size: 200%; 
}
}

@font-face {
    font-family: 'rust';
