.navbar {
    position: fixed;
    display: block;
    width: 100%;
    height: 60px;
    overflow: hidden;
    top: 50px;
    left: 50px;
    margin: 0;
    opacity: 1;
    z-index: 99;
}

.navbar a {
    margin: 0%;
    padding: 0%;
    list-style: none;
    font-family: 'rust'!important;
    display: inline-block;
    width: 200px;
    height: 40px;
    color:#fff;
    opacity: 1;
    line-height: 40px;
    text-align: center;
    font-size: 30px;
    font-weight: bold;
    -webkit-transition: all .4s ease-in-out;
    transition: all .4s ease-in-out;

}

@media (max-width: 768px){
    .navbar ul{
        display:none
    }
}
@media (min-width: 769px){
    .toggle-btn {
        display:none
    }
}



.toggle-button{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 25px;
    width: 30px;
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 0;
    box-sizing: border-box;
}

.toggle-button:focus {
    outline:none
}

.line {
    width: 60px;
    height: 4px;
    margin-bottom: 10px;
    background-color:skyblue;
    -webkit-transition: all .4s ease-in-out;
    transition: all .4s ease-in-out;
}
.resume-button{
    position: fixed;
    right: 60px;
    top:70px
    
}

@media (max-width: 768px){
    .resume-button {
        display:none
    }
}
.side-drawer{
    height: 100%;
    background: white;
    box-shadow: 2px 0px 5px rgba(0,0,0, 0.5);
    position: fixed;
    top: 0;
    left: 0;
    width:70%;
    max-width: 400px;
    z-index: 1;
    -webkit-transform:translateX(-100%);
            transform:translateX(-100%);
    -webkit-transition: -webkit-transform 0.3s ease-out;
    transition: -webkit-transform 0.3s ease-out;
    transition: transform 0.3s ease-out;
    transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
}

.side-drawer.open{
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
.side-drawer ul {
    height: 100%;
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.side-drawer li {
    margin: 3rem 0 ;
} 

.side-drawer a {
    color: lightskyblue;
    text-decoration: none;
    font-size: 4rem;
}

@media (min-width: 796px){
    .side-drawer{
        display: none;
    }
}
