.workSection{
    height: 950px;
    background-image: url('/public/assets/images/sun.jpg');
    background-size: cover;

}



.work-section {
	max-width: 960px;
	margin:0 auto;
	padding:6% 2%;
}

.work-grid {
    display: flex;
    position: absolute;
    vertical-align: center;
	margin: 150px 0 0 0;
	padding: 0;
	list-style: none;
	
	text-align: center;
	width: 100%;
}

.work-grid:after {
	clear: both;
}

.work-grid:after,
.work-box:before {
	content: '';
    display: table;
}

.work-grid li {
	width: 200px;
	height: 200px;
	display: inline-block;
	margin: 10px;
}
.work-grid2 {
    display: flex;
    position: absolute;
    vertical-align: center;
	margin: 450px 0 0 0;
	padding: 0;
	list-style: none;
	text-align: center;
	width: 100%;
}

.work-grid2:after {
	clear: both;
}

.work-grid2:after,
.work-box:before {
	content: '';
    display: table;
}

.work-grid2 li {
	width: 200px;
	height: 200px;
	display: inline-block;
	margin: 10px;
}


.work-box {
	width: 100%;
	height: 100%;
	position: relative;
	cursor: pointer;
    border-radius: 10px;
    -webkit-transition: 0.3s ease-in-out,
    -webkit-transform 0.3s ease-in-out;
    -webkit-transition: all 0.3s ease-in-out,
    -webkit-transform 0.3s ease-in-out;
    transition: all 0.3s ease-in-out,
    -webkit-transform 0.3s ease-in-out;
    transition: all 0.3s ease-in-out,
    transform 0.3s ease-in-out;
    transition: all 0.3s ease-in-out,
    transform 0.3s ease-in-out,
    -webkit-transform 0.3s ease-in-out;
}

.work-box:hover {
	-webkit-transform: scale(1.05);
	        transform: scale(1.05);
}

